<template>
  <input type="checkbox" :class="classes" :style="style">
</template>

<script>
import './checkbox.css';

export default {
  name: 'checkbox',

  props: {
    label: {
      type: String,
      required: true,
    },
    primary: {
      type: Boolean,
      default: false,
    },
    sizeType: {
      type: String,
      default: 'primary',
      validator: function (value) {
        return ['large', 'medium', 'small'].indexOf(value) !== -1;
      },
    },
    backgroundColor: {
      type: String,
    },
  },

  computed: {
    classes() {
      return {
        'checkbox': true,
        'checkbox--primary': this.primary,
        [`checkbox--${this.ctaType}`]: true,
      };
    },
    style() {
      return {
        backgroundColor: this.backgroundColor,
      };
    },
  },

  // methods: {
  //   onClick() {
  //     this.$emit('onClick');
  //   },
  // },
};
</script>
